<template>
  <el-row v-loading="loading">
    <el-col :span="24">
      <div style="text-align: left;">
        <el-button type="text" icon="el-icon-back" @click="goBack" style="padding-bottom: 0;"></el-button>
      </div>
    </el-col>
    <el-col :span="24">
      <div>
        <div><h3 style="margin-top: 0;margin-bottom: 0">{{ title }}</h3></div>
        <div style="width: 100%;margin-top: 5px;display: flex;justify-content: flex-end;align-items: center">
          <span
              style="color: #20a0ff;margin-right:20px;font-size: 16px;">: {{ rank }}
          </span>
          <span
              style="color: #20a0ff;margin-right:20px;font-size: 16px;">: {{ hotValve }}
          </span>
          <span
              style="color: #20a0ff;margin-right:20px;font-size: 16px;">: {{ count }}
          </span>
          <span
              style="color: #20a0ff;margin-right:20px;font-size: 16px;">: {{ mapList[0].value }}
          </span>
          <span style="margin:0 50px 0 0">

          </span>
        </div>
        <div>
          <el-col :span="4" v-for="(item,index) in videoList" :key="index" align="left" style="border: 5px">
            <div style="width: 100%;height: 1px;background-color: #20a0ff;margin-top: 8px;margin-bottom: 0"></div>
            <video-player
                class="video-player vjs-custom-skin"
                ref="videoPlayer"
                :playsinline="true"
                :options="playerOptions[index]"
                @play="onPlayerPlay($event)"
                @pause="onPlayerPause($event)"
                @ended="onPlayerEnded($event)"
                @waiting="onPlayerWaiting($event)"
                @playing="onPlayerPlaying($event)"
                @loadeddata="onPlayerLoadeddata($event)"
                @timeupdate="onPlayerTimeupdate($event)"
                @canplay="onPlayerCanplay($event)"
                @canplaythrough="onPlayerCanplaythrough($event)"
                @statechanged="playerStateChanged($event)"
                @ready="playerReadied"
            >
            </video-player>
            <!--            <el-row>
                          <img class="img_style" :src="item.img_url" alt="" :title="item.title" @click="itemClick(item)"/>
                        </el-row>-->
            <el-row>
                <span style="color: #409eff;cursor: pointer" @click="itemClick(item)">
                  {{ item.title }}
                </span>
            </el-row>
          </el-col>
        </div>
      </div>
    </el-col>
    <el-col>
    </el-col>
  </el-row>
</template>
<script>

import {getRequest, postRequest} from "../../utils/api";

export default {
  methods: {
    goBack() {
      this.$router.go(-1);
    },

    itemClick(row) {
      window.open(row.link);
    },

    // 
    onPlayerPlay(player) {
      console.log('player play!', player)
    },

    // 
    onPlayerPause(player) {
      console.log('player pause!', player)
    },

    // 
    onPlayerEnded($event) {
      console.log($event)
    },

    // DOMreadyState
    onPlayerWaiting($event) {
      console.log($event)
    },

    // 
    onPlayerPlaying($event) {
      console.log($event)
    },

    // 
    onPlayerLoadeddata($event) {
      console.log($event)
    },

    // 
    onPlayerTimeupdate($event) {
      console.log($event)
    },

    //readyStateHAVE_FUTURE_DATA
    onPlayerCanplay(player) {
      // console.log('player Canplay!', player)
    },

    //readyStateHAVE_ENOUGH_DATA
    onPlayerCanplaythrough(player) {
      // console.log('player Canplaythrough!', player)
    },

    //
    playerStateChanged(playerCurrentState) {
      console.log('player current update state', playerCurrentState)
    },

    //ready
    playerReadied(player) {
      console.log('example player 1 readied', player);
    },

    getRealVideoUPath(videoList) {
      var _this = this;
      var linkList = [];
      for (var v = 0; v < videoList.length; v++) {
        linkList.push(videoList[v].link);
      }

      postRequest("/douYin/getRealVideoUPath", {
        linkList: linkList
      }).then(resp => {
        if (resp.status == 200) {
          var urlMaps = resp.data; // keyidvalue
          for (var key in urlMaps) {
            console.log("load... key" + key + "value" + urlMaps[key]);
          }
          this.buildVideoPlayers(videoList, urlMaps);
        } else {
          _this.$message({type: 'error', message: '!'});
        }
      }, resp => {
        console.log("load error1... " + resp);
      }).catch(resp => {
        console.log("load error2... " + resp);
      })
    },

    buildVideoPlayers(videoList, urlMaps) {
      var _this = this;
      var videoPlayers = [];
      for (var i = 0; i < videoList.length; i++) {
        var video = videoList[i];
        var videoIndex = video.link.lastIndexOf("video");
        var index = video.link.lastIndexOf("?");
        var itemId = video.link.substring(videoIndex + 6, index - 1); // itemId

        var videoPlayer = {
          playbackRates: [0.5, 1.0, 1.5, 2.0], // 
          autoplay: false, // true,
          muted: false, // 
          loop: false, // 
          preload: 'auto', // <video>auto,
          language: 'zh-CN',
          aspectRatio: '9:16', //  - "16:9""4:3"
          fluid: true, // trueVideo.js player
          sources: [{
            type: "video/mp4", // 
            src: urlMaps[itemId] // urleghttps://aweme.snssdk.com/aweme/v1/play/?video_id=v0d00fg10000c9ek54jc77u7pk9s2k30&ratio=720p&line=0
          }],
          poster: video.img_url, // 
          notSupportedMessage: '', // Video.js
          controlBar: {
            timeDivider: true, // 
            durationDisplay: true, // 
            remainingTimeDisplay: false, // 
            fullscreenToggle: true // 
          }
        }
        videoPlayers.push(videoPlayer);
      }
      _this.playerOptions = videoPlayers;
    }

  },
  mounted: function () {

    var douYinDetail = this.$route.query.data;
    this.activeName = this.$route.query.activeName;
    var _this = this;
    this.loading = true;
    _this.title = douYinDetail.title;
    _this.hotValve = douYinDetail.value;
    _this.mapList = douYinDetail.map_list;
    _this.rank = douYinDetail.rank;
    _this.count = douYinDetail.count;
    var videoList = douYinDetail.extra_list;
    _this.videoList = videoList;

    this.getRealVideoUPath(videoList);
    this.loading = false;
  },
  data() {
    return {
      douYinDetail: {},
      mapList: [],
      videoList: [],
      loading: false,
      activeName: '',
      title: '',
      hotValve: 0,
      rank: 0,
      count: 0,
      playerOptions: []
    }
  }
}
</script>

<style type="text/css">
.img_style {
  cursor: pointer;
}
</style>


